import React from 'react';

const ButtonInLine = ({
  onClick,
  type = 'button',
  children
}) =>
  <Button
    type={type}
    className='button-inline'
    onClick={onClick}
  >
    {children}
  </Button>

const Button = ({
  onClick,
  className,
  type = 'button',
  children
}) =>
  <button
    type={type}
    className={className}
    onClick={onClick}
  >
    {children}
  </button>

export default Button;

export {
  ButtonInLine
}
